			} else if (this.zdTitle == '') {
				this.queryCpData(1);
			} else if (this.zdTitle == '') {
				this.queryCbRy();
			}
		},
		queryComplainUser(data) {
			this.cpKeyword = data;
			if (this.zdTitle == '') {
				this.queryUser(1);
			} else if (this.zdTitle == '') {
				this.queryCpData(1);
			} else if (this.zdTitle == '') {
				this.queryCbRy();
			}
		},
		// 
		handleCommitZdr(data) {
			if (this.zdTitle == '') {
				saveAnswer({
					//
					zbwid: this.wid,
					nr: data.nr,
					sfjsr: data.nickName,
					jsrbh: data.userName,
					qwjjrq: data.qwjjrq,
					hflx: 6
				}).then(({ data }) => {
					this.zdfzrVisible = false;
					if (data.state == 'success') {
						let keysArr = Object.keys(data.data);
						if (keysArr[0] == 'true') {
							this.zdfzrVisible = false;
							this.queryAnswers(this.wid);
							this.queryProcess(this.wid);
							this.queryQuestion(this.wid);
						}
						this.$alert(keysArr[0] == 'true' ? data.data.true : data.data.false, '', {
							confirmButtonText: '',
							type: keysArr[0] == 'true' ? 'success' : 'error'
						});
					} else {
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'error'
						});
					}
				});
			} else if (this.zdTitle == '') {
				saveYyzfData({
					wid: this.wid,
					nr: data.nr,
					ryData: data.yyryData.join('|'),
					bh: data.lchj, //
					qwjjrq: data.qwjjrq
				}).then(({ data }) => {
					if (data.state == 'success') {
						let keysArr = Object.keys(data.data);
						if (keysArr[0] == 'true') {
							this.zdfzrVisible = false;
							this.queryAnswers(this.wid);
							this.queryProcess(this.wid);
							this.queryQuestion(this.wid);
						}
						this.$alert(keysArr[0] == 'true' ? data.data.true : data.data.false, '', {
							confirmButtonText: '',
							type: keysArr[0] == 'true' ? 'success' : 'error'
						});
					} else {
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'error'
						});
					}
				});
			} else if (this.zdTitle == '') {
				this.saveCb(data.yyryData.join('|'));
			}
		},
		handleClose() {
			this.ClosezdfzrVisible();
			this.zdfzrVisible = false;
		},
		ClosezdfzrVisible() {
			this.cpKeyword = '';
		},

		queryUser(curPage) {
			// 
			queryUser({
				curPage: curPage,
				pageSize: 10,
				Isexternal: false,
				keyword: this.cpKeyword
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.tableData = data.data.rows;
					this.jkrTotal = data.data.records;
				}
			});
		},

		queryCpData(curPage) {
			// 
			queryZjCpData({
				curPage: curPage,
				pageSize: 10,
				rylb: this.rylb,
				keyword: this.cpKeyword
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.tableData = data.data.rows;
					this.jkrTotal = data.data.records;
				}
			});
		},
		queryCbRy() {
			//
			queryCbRy({
				wid: this.wid,
				keyword: this.cpKeyword
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.tableData = data.data;
					this.tableData.forEach((ele, i, arr) => {
						if (ele.username.slice(0, 1) == 1) {
							ele.userbh = `${ele.username.slice(0, 3)}****${ele.username.slice(-4)}`;
						} else {
							ele.userbh = ele.username;
						}
					});
				}
			});
		},
		saveCb(ryData) {
			//
			saveCb({
				wid: this.wid,
				nr: $('#summernoteT').summernote('code') == '<p><br></p>' ? '' : $('#summernoteT').summernote('code'),
				ryData: ryData
			}).then(({ data }) => {
				if (data.state == 'success') {
					$('#summernoteT').summernote('code', '');
					this.$message({
						message: '',
						type: 'success'
					});
					this.queryAnswers(this.wid);
					if (ryData) {
						this.zdfzrVisible = false;
					}
				}
			});
		},
		//
		forwardingProblem() {
			let that = this;
			this.zfcplist = [];
			this.innerZFWTisible = true;
			forwardLabel({
				wid: this.wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					if (data.data.length != 0 && data.data != null) {
						this.zfDx = data.data;
						this.radio = data.data[0].bh;
					}
				}
			});
			this.$nextTick(() => {
				$('#summernoteZF').summernote({
					dialogsInBody: true,
					placeholder: '',
					height: 200,
					width: 100 + '%',
					minHeight: 150,
					maxHeight: 400,
					lang: 'zh-CN',
					focus: true,
					toolbar: [
						['style', ['bold', 'italic', 'underline', 'clear']],
						['font', ['strikethrough', 'superscript', 'subscript']],
						['fontsize', ['fontsize']],
						['color', ['color']],
						['para', ['ul', 'ol', 'paragraph']],
						['height', ['height']],
						['picture'],
						['link', ['linkDialogShow', 'unlink']]
					]
				});
			});
			this.$get(this.API.queryResponsibleProduct, {
				xmbh: '',
				internalProject: !this.qusetionInfo.xmbh ? true : ''
			}).then(res => {
				if (res.state == 'success') {
					Object.keys(res.data).forEach(function(key) {
						that.zfcplist.push({ mc: res.data[key], label: key });
					});
				}
			});
		},
		//()
		CommitZFWT() {
			if (this.radio == 11 && this.zfcp == '') {
				this.$alert('', '', {
					confirmButtonText: '',
					type: 'warning'
				});
				return;
			}
			if (this.radio == 66 && this.userIds == '') {
				this.$alert('', '', {
					confirmButtonText: '',
					type: 'warning'
				});
				return;
			}
			if (!this.qwjjrqZf) {
				this.$alert('', '', {
					confirmButtonText: '',
					type: 'warning'
				});
				return;
			}
			if ($('#summernoteZF').summernote('code') == '<p><br></p>') {
				this.$alert('', '', {
					confirmButtonText: '',
					type: 'warning'
				});
				return;
			}

			saveForward({
				wid: this.wid,
				bh: this.radio,
				nr: $('#summernoteZF').summernote('code') == '<p><br></p>' ? '' : $('#summernoteZF').summernote('code'),
				cpbh: this.radio == 11 ? this.zfcp : '',
				qwjjrq: this.qwjjrqZf,
				userIds: this.radio == 66 ? this.userIds : ''
			}).then(({ data }) => {
				if (data.state == 'success') {
					let keysArr = Object.keys(data.data);
					if (keysArr[0] == 'true') {
						this.innerZFWTisible = false;
						this.queryAnswers(this.wid);
						this.queryBtnAuth(this.wid);
						this.queryProcess(this.wid);
						this.queryQuestion(this.wid);
					}
					this.$alert(keysArr[0] == 'true' ? data.data.true : data.data.false, '', {
						confirmButtonText: '',
						type: keysArr[0] == 'true' ? 'success' : 'error'
					});
				}
			});
		},
		CancelZFWT() {
			// 
			this.innerZFWTisible = false;
		},

		//
		remindsQuestion() {
			this.zdTitle = '';
			//
			showCbCondition({
				wid: this.wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					if (data.data == 1) {
						this.queryCbRy();
						this.zdfzrVisible = true;
					} else if (data.data == 0) {
						this.saveCb('');
					}
				}
			});
		},

		//
		replyQuestion(param) {
			let reg = /^\d+(\.\d+)?$/;
			let hfnr = $('#summernoteT').summernote('code');
			if (!reg.test(this.gsValue) || this.gsValue > 8) {
				this.$alert('8,8!', '', {
					confirmButtonText: '',
					type: 'warning'
				});
				return;
			}

			if (hfnr == '<p><br></p>' || hfnr == '') {
				this.$alert('', '', {
					confirmButtonText: '',
					type: 'warning'
				});
			} else {
				this.isHF = true;
				saveAnswer({
					//
					zbwid: this.wid,
					nr: hfnr,
					gs: this.gsValue,
					sfbg: this.sfbgValue,
					cnjsrq: this.cnjsrq || '',
					hflx: param == 'hf' ? 1 : 99,
					tktwr: this.tktwr
				}).then(({ data }) => {
					if (data.state == 'success') {
						this.isHF = false;
						this.gsValue = 0;
						this.$message({
							message: '',
							type: 'success'
						});
						this.queryAnswers(this.wid);
						this.queryProcess(this.wid);
						$('#summernoteT').summernote('code', '');
						clearTimeout(this.timer);
						localStorage.removeItem(this.wid);
					} else {
						this.isHF = false;
						this.$alert(data.msg, '', {
							confirmButtonText: '',
							type: 'error'
						});
					}
				});
			}
		},
		//
		closeQuestion() {
			// 12.03
			canClose({
				wid: this.wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					if (!data.data) {
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'warning'
						});
					} else {
						if (this.qusetionInfo.zlpf != '') {
							this.fwzlValue = JSON.parse(this.qusetionInfo.zlpf);
						}
						queryrReferenceHour({
							wid: this.wid
						}).then(({ data }) => {
							if (data.state == 'success') {
								this.ContributionPeople = data.data;
								this.ContributionPeople.forEach((ele, i, arr) => {
									ele.qrgs = ele.confirmH.replace('/^.(d+)/', '0.');
								});
							}
						});
						this.innerVisible = true;
					}
				}
			});
		},
		commitQuestion() {
			//
			let gxrArr = [];
			if (this.ContributionPeople.length != 0) {
				this.ContributionPeople.forEach((ele, i, arr) => {
					gxrArr.push(ele.fbrxm + ',' + ele.fbrbh + ',' + ele.qrgs);
				});
			}
			this.$confirm('?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			})
				.then(() => {
					if (this.fwzlValue == 0) {
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'warning'
						});
						return;
					}
					if (this.fwzlValue <= 3 && !this.cpsm) {
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'warning'
						});
						return;
					}
					closeQuestion({
						wid: this.wid,
						zlpf: this.fwzlValue,
						gxrData: gxrArr.join('|'),
						sfjj: '',
						jjsm: $('#summernoteT').summernote('code') == '<p><br></p>' ? '' : $('#summernoteT').summernote('code'),
						cpsm: this.fwzlValue <= 3 ? this.cpsm : '',
						gssfrk: this.gssfrk
					}).then(({ data }) => {
						if (data.state == 'success') {
							this.innerVisible = false;
							this.$alert(data.msg, '', {
								confirmButtonText: '',
								type: 'success',
								callback: action => {
									this.queryQuestion(this.wid);
									this.queryAnswers(this.wid);
									this.queryBtnAuth(this.wid);
									this.queryProcess(this.wid);
								}
							});
						} else {
							this.$alert(data.msg, '', {
								confirmButtonText: '',
								type: 'error'
							});
						}
					});
				})
				.catch(() => {});
		},
		queryProcess(wid) {
			queryProcess({
				//
				wid: wid,
				isTs: ''
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.questionProcess = data.data;
					this.questionProcess.forEach((ele, i, arr) => {
						if (ele.flag == 1) {
							this.questionProcess[i].status = 'process';
						} else if (ele.flag == 2) {
							this.questionProcess[i].status = 'success';
						}
					});
					this.lchjList = this.questionProcess.slice(0, this.questionProcess.length - 2);
				}
			});
		},

		// 
		queryQuestion(wid, xmtd) {
			//   
			this.qusetionInfo = {};
			this.wtlbmc = '';
			this.wtlymc = '';
			this.wtjbmc = '';
			this.hjgs = 0;
			queryQuestion({
				wid: wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.qusetionInfo = data.data;
					this.wtly = data.data.wtly;
					this.cnjsrq = data.data.qwjjrq;
					document.title = '-' + data.data.bt;
					if (this.qusetionInfo.fbzt == 1) {
						$('#summernoteT').summernote('destroy');
					}
					if (xmtd == 1) {
						isgcXmtdbyWt({
							wid: this.qusetionInfo.wid
						}).then(({ data }) => {
							if (data.state == 'success') {
								this.isgcXmtdbyWt = data.data;
							}
						});
					}
					if (this.qusetionInfo.wtlb != '') {
						getResponsibleTaskList({
							name: 'ProblemType',
							code: this.qusetionInfo.wtlb
						}).then(({ data }) => {
							if (data.state == 'success') {
								this.wtlbmc = data.data;
							}
						});
					}

					if (this.qusetionInfo.wtly != '') {
						getResponsibleTaskList({
							name: 'ProblemSource',
							code: this.qusetionInfo.wtly
						}).then(({ data }) => {
							if (data.state == 'success') {
								this.wtlymc = data.data;
							}
						});
					}

					if (this.qusetionInfo.wtjb != '') {
						getResponsibleTaskList({
							name: 'ProblemLevel',
							code: this.qusetionInfo.wtjb
						}).then(({ data }) => {
							if (data.state == 'success') {
								this.wtjbmc = data.data;
							}
						});
					}

					//    
					if (data.data.fbzt == 1) {
						this.zlpf = JSON.parse(data.data.zlpf);
						queryContributionPeople({
							//
							wid: this.wid
						}).then(({ data }) => {
							if (data.state == 'success') {
								if (data.data != null && data.data.length != 0) {
									this.yxGXR = data.data;
									this.yxGXR.forEach((ele, i, arr) => {
										if (ele.gs.split('.')[0] == '') {
											ele.gs = 0 + ele.gs;
										}
										this.hjgs += parseFloat(ele.gs);
									});
								} else {
									this.yxGXR = [];
								}
							}
						});
					}
					try {
						this.qusetionInfo.nr = decodeURIComponent(this.qusetionInfo.nr).replace(
							/gcgl\/sys\/emapcomponent\/file\/getFileByToken/g,
							'emap/sys/emapcomponent/file/getFileByToken'
						);
					} catch (err) {
						this.qusetionInfo.nr = this.qusetionInfo.nr;
					}
				}
			});
		},
		// 
		queryAnswers(wid) {
			this.questionReply = [];
			queryAnswers({
				wid: wid,
				isSolution: this.isCjwt ? true : ''
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.questionReply = data.data;
					if (data.data != null && data.data.length != 0) {
						this.questionReply.forEach((ele, i, arr) => {
							try {
								ele.nr = decodeURIComponent(ele.nr).replace('/gcgl/sys/emapcomponent/file/getFileByToken/', '/emap/sys/emapcomponent/file/getFileByToken/');
							} catch (err) {
								ele.nr = ele.nr;
							}
						});
					}
				}
			});
		},
		
		//  
		queryBtnAuth(wid) {
			queryBtnAuth({
				wid: wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.btnShwon = data.data;
					this.xgqwjjrq = data.data.qwjjrq;
				}
			});
		},
		
		//
		queryLabel(wid) {
			this.sftj = [];
			queryLabel({
				wid: wid
			}).then(({ data }) => {
				if (data.state == 'success') {
					this.wtbq = data.data;
					this.wtbq.forEach((ele, i, arr) => {
						if (ele.sjly == 0) {
							ele.type = 'info';
						} else {
							ele.type = '';
						}
						if (ele.sjly == 2) {
							this.sftj.push(ele.dm);
						}
					});
				}
			});
		},

		getCrowdYwx() {
			this.$get(this.API.getCrowdYwx, {}).then(res => {
				if (res.state == 'success') {
					this.CrowdYwx = res.data;
				}
			});
		},
		getCrowdCp(ywxbm) {
			this.$get(this.API.getCrowdCp, { ywxbm: ywxbm }).then(res => {
				if (res.state == 'success') {
					this.CrowdCp = res.data;
				}
			});
		}
	},
	watch: {
		crowdVisible(n, o) {
			if (!!n) {
				this.crowdxqData.zbjzrq = GetDateStr(7);
			}
		},
		crowdType(n, o) {
			if (n == 2) {
				this.getCrowdYwx();
			}
		}
	},
	activated() {},
	components: {
		pagination,
		itemChoose,
		zdfzrChoose,
		sqjsForm,
		twDialog,
		xxDialog,
		xmtdcylog,
		uploadFile,
		sqgqwtDialog
	}
};
</script>
