	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	vertical-align: bottom;
}
/* */
.question-sqdyz-title:before {
	content: '*';
	color: #f00;
	padding: 0 2px;
}
.item-wtbq {
	float: left;
	padding: 2px 0;
}
.item-wtbq-tag {
	display: inline-block;
	width: 95%;
	white-space: pre-wrap;
}
.filter-bt {
	display: inline-block;
	width: 100px;
	vertical-align: top;
	text-align: right;
	font-weight: 700;
}
.question_transmit .span_label {
	display: inline-block;
	width: 110px;
}

.isRedColor {
	color: #f00;
	font-weight: 700;
}

.send_crowd img {
	width: 100%;
}
.send-crowd_topic {
	border: 1px solid #e4eaec;
	background: #f3f7f9;
	border-radius: 3px;
	height: 40px;
	line-height: 40px;
	padding: 0 6px;
}

.crowd-rqgz h4 {
	font-weight: 700;
	font-size: 13px;
	margin: 10px 0 !important;
}
.crowd-rqgz p {
	font-size: 12px;
	text-indent: 2em;
}
</style>



